<template>
  <div class="home">
    <van-sticky>
      <van-nav-bar title="优惠券" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <div class="noData" v-if="ticketList.length == 0">
      暂无数据!
    </div>
    <div class="couponList" v-else>
      <div
        class="couponItem"
        v-for="(item, index) in ticketList"
        :class="
          item.is_use == 1 || item.expire == 1
            ? 'couponItemUsed'
            : 'couponItemUnuse'
        "
        @click="chooseCoupon(item, index)"
      >
        <div class="couponPrice leftEl"><span>￥</span>{{ item.price }}</div>
        <div class="itemRight rightEl">
          <div class="itemTitle">优惠券</div>
          <div class="itemTime">有效时间:{{ item.expire_time }}</div>
          <img
            class="selIcon"
            src="../../assets/imgs/personal/couponSel.png"
            alt=""
            v-if="
              index == activeIndex && item.is_use == 0 && item.expire == 0
            "
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { myTicket } from "@/api/personal";
export default {
  data() {
    return {
      ticketList: [],
      activeIndex: -1,
      newPath: ""
    };
  },
  created() {
    this.getTicket();
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.newPath = from.name;
    });
  },
  methods: {
    getTicket() {
      myTicket().then(res => {
        this.ticketList = res;
      });
    },
    onClickLeft() {
      if (this.newPath == "Persoanal"||this.newPath == "Mall") {
        this.$router.go(-1);
      } else {
        this.$router.push({
            path: this.newPath,
            query: { chooseTicket: this.chooseTicket }
          });
      }
    },
    chooseCoupon(item, index) {
      if (item.is_use == 0 && item.expire == 0) {
        this.activeIndex = index;
        this.chooseTicket = item;
      } else {
        localStorage.setItem("chooseTicket", {});
        this.$toast("无法使用此优惠券！");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100vh;
  background: #f1f1f1;
}
.couponList {
  padding: 10px 15px;
  box-sizing: border-box;
  width: 100%;
}
.couponItem {
  width: 100%;
  overflow: hidden;
  height: 80px;
  position: relative;
  margin-bottom: 10px;
}
.couponItemUnuse {
  background: url(../../assets/imgs/personal/couponBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
}
.couponItemUsed {
  background: url(../../assets/imgs/personal/couponBg1.png) 0 0 no-repeat;
  background-size: 100% 100%;
}
.couponPrice {
  color: #fff;
  font-size: 18px;
  line-height: 80px;
  width: 30%;
  text-align: center;
  span {
    font-size: 13px;
  }
}
.itemRight {
  width: 70%;
  padding: 15px 0 2px 18px;
  box-sizing: border-box;
  font-size: 14px;
  color: #333;
  .itemTime {
    margin-top: 13px;
    color: #ff8726;
  }
}
.selIcon {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 25%;
  right: 4%;
}
</style>
